      padding-right: 14px; } }

.container-sm {
  max-width: 660px;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px; }
  @media (max-width: 992px) {
    .container-sm {
      max-width: 600px; } }
  @media (max-width: 767px) {
    .container-sm {
      width: 100%;
