  @media (max-width: 767px) {
    .section {
      padding-top: 40px;
      padding-bottom: 40px; } }

.bg-primary {
  background-color: #AA0601; }

.flex {
  display: flex; }

.wrap {
  flex-wrap: wrap; }

.justify-between {
  justify-content: space-between; }

.align-item-center {
  align-items: center; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px; }
  .header .logo {
    width: 80px;
    height: 72px;
    margin: 4px 0;
    background-image: url("https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/logo-white.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain; }
  .header ul {
    gap: 64px; }
    .header ul li {
      padding: 25px 0; }
    .header ul a {
      font-family: "Noto Sans TC", sans-serif;
      font-size: 20px;
      line-height: 1.5;
      color: white; }
