      .header ul {
        gap: 0px;
        width: 100%;
        text-align: center; }
        .header ul li {
          width: 50%;
          padding: 12px 0;
          border: 1px solid white; }
        .header ul a {
          font-size: 16px; } }

.footer-contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 48px;
  border-bottom: 1px solid white; }
  .footer-contact .links {
    display: flex;
    gap: 48px; }
    .footer-contact .links a {
      font-family: "Noto Sans TC", sans-serif;
      font-size: 16px;
      line-height: 1.5;
      color: white; }
      .footer-contact .links a:hover {
        color: black; }
  .footer-contact .social-media {
    display: flex;
    gap: 32px; }
    .footer-contact .social-media img {
      width: 48px; }
  .footer-contact .contact-way {
    width: 100%;
    font-size: 36px;
    line-height: 1.5;
    font-family: "Noto Sans TC", sans-serif;
    margin: 24px 0;
    display: flex;
    flex-direction: column;
    gap: 8px; }
    .footer-contact .contact-way span {
      margin-right: 16px; }
    .footer-contact .contact-way a {
      color: white; }
      .footer-contact .contact-way a:hover {
        color: black; }
  @media (max-width: 767px) {
    .footer-contact {
      flex-direction: row-reverse;
      align-items: flex-start;
      padding-top: 24px; }
      .footer-contact .links {
        display: none; }
      .footer-contact .social-media {
        gap: 8px; }
        .footer-contact .social-media img {
          width: 24px; }
      .footer-contact .contact-way {
        width: initial;
        font-size: 16px;
        margin-top: 0; }
        .footer-contact .contact-way span {
          margin-right: 8px; } }

.footer-policy {
  display: flex;
  justify-content: space-between;
  color: white;
  padding: 32px 0; }
  .footer-policy .copyright {
    font-size: 16px;
    line-height: 1.5;
    font-family: "Roboto", sans-serif; }
  .footer-policy .tnc {
    display: flex;
    gap: 48px;
    font-size: 14px;
    line-height: 1.5; }
    .footer-policy .tnc a {
      color: white;
      font-family: "Noto Sans TC", sans-serif; }
      .footer-policy .tnc a:hover {
        color: black; }
  @media (max-width: 767px) {
    .footer-policy {
      flex-direction: column;
      gap: 8px;
      padding-top: 16px;
      padding-bottom: 24px; }
      .footer-policy .tnc {
        flex-direction: column;
        gap: 8px;
        font-size: 16px; } }

.banner {
  background-image: url("https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/home-header.png");
  background-size: cover;
  background-position: center; }
  .banner .slogan {
