.feature {
  background-color: #FBF2F2; }
  .feature h2 {
    margin-bottom: 24px; }
  .feature .value {
    display: flex;
    gap: 24px;
    text-align: center;
    justify-content: center; }
    .feature .value li {
      width: calc((100% - 24px) / 4); }
    @media (max-width: 992px) {
      .feature .value {
        flex-wrap: wrap; }
        .feature .value li {
          width: calc((100% - 24px) / 2); } }
    @media (max-width: 767px) {
      .feature .value li {
        width: 100%; } }
  .feature img {
    max-width: 100px;
    margin-bottom: 16px; }
  .feature h3 {
    font-family: "Noto Sans TC", sans-serif;
    font-size: 24px;
    font-weight: 700;
    color: #650300;
    margin-bottom: 8px;
    font-style: normal; }
  .feature p {
    text-align: left;
    font-family: "Noto Sans TC", sans-serif;
    font-size: 16px;
    line-height: 1.5; }

.classical h2 {
  margin-bottom: 48px; }

.classical .products {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px; }
  .classical .products li {
    width: calc((100% - 24px) / 3); }
  .classical .products img {
